<template>
  <b-modal
    id="modal-add-delivery"
    no-close-on-backdrop
    no-fade
    size="lg"
    ref="modal-add-delivery"
    ignore-enforce-focus-selector="span.select2-container"
  >
    <template #modal-title>{{ title }}</template>
    <div class="form-group">
      <label for="">Hợp đồng</label>
      <select2
        v-model="Delivery.contract_id"
        :options="select.contracts"
        :settings="settings"
      />
    </div>
    <template #modal-footer>
      <button @click="store" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
      <button class="btn btn-secondary" @click="close">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
  </b-modal>
</template>

<script>
import Select2 from "v-select2-component";
export default {
  components: {
    // datePicker,
    Select2,
  },
  data() {
    return {
      title: "Thêm mới BBBG",
      select: {
        contracts: [],
      },
      Delivery: {
        contract_id: 0,
      },
      settings: {
        placeholder: "--Hãy chọn dữ liệu--",
        theme: "bootstrap-5",
      },
    };
  },
  methods: {
    show(item = {}) {
      this.$bvModal.show("modal-add-delivery");
      this.getContracts();
    },
    async store() {
      
    },
    close() {
      this.$bvModal.hide("modal-add-delivery");
    },
    async getContracts() {
      await axios
        .post("contracts", {
          page: 1,
          pageSize: 500,
        })
        .then((response) => {
          this.contracts = response.data.data;
          this.select.contracts = response.data.data.map((c) => {
            return {
              id: c.contract_id,
              text: c.company_name + ` (${c.details[0].detail_content})`,
            };
          });
        });
    },
  },
};
</script>
