<template>
  <div>
    <h1 class="fw-bold title-page">Danh sách BBBG</h1>

    <div class="row mb-2">
      <div class="col-12">
        <div class="input-group">
          <input
            type="search"
            v-model="company_name"
            placeholder="Nhập mã số thuế hoặc tên doanh nghiệp để tìm kiếm...."
            class="form-control"
            v-on:change="search()"
          />
          <button
            @click="search()"
            type="button"
            id="button-addon2"
            class="btn btn-primary-soft"
          >
            <i class="fa-solid fa-magnifying-glass"></i> Tìm kiếm
          </button>
          <router-link class="btn btn-primary ms-2" :to="{ name: 'delivery-add' }">
            <i class="fa-duotone fa-circle-plus"></i> Tạo mới
          </router-link>
          <!-- <button class="btn btn-primary ms-2" @click="show()">
            <i class="fa-duotone fa-circle-plus"></i> Tạo mới
          </button> -->
        </div>
      </div>
    </div>
    <modal-delivery ref="modalDelivery"></modal-delivery>
    <table class="table table-striped">
      <thead class="table-dark">
        <tr>
          <th>#</th>
          <th>Tên công ty</th>
          <th>Mã số thuế</th>
          <th>Tên TB</th>
          <th>Serial Number</th>
          <th>Loại</th>
          <th>Nhân viên</th>
          <th>Thời gian</th>
          <th>Bên nhận</th>
          <th width="100"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in deliverys" :key="item.delivery_id">
          <td>{{ item.delivery_id }}</td>
          <td>
            <router-link
              :to="{
                name: 'delivery-edit',
                params: { id: item.delivery_id },
              }"
            >
              {{ item.company_name }}
            </router-link>
          </td>
          <td>{{ item.company_taxcode }}</td>
          <td>{{ item.ten_tb }}</td>
          <td>{{ item.serial_number }}</td>
          <td>{{ deliveryType(item.delivery_type) }}</td>
          <td>{{ item.staff_name }}</td>
          <td>{{ item.delivery_date | moment("DD/MM/YYYY") }}</td>
          <td>{{ item.recipient_name }}</td>
          <td>
            <!-- <button class="btn btn-warning btn-round opacity-25" @click="show(item)">
              <i class="fa-regular fa-pen-to-square"></i>
            </button> -->
            <router-link
              class="btn btn-warning btn-round btn-sm me-1"
              :to="{
                name: 'delivery-edit',
                params: { id: item.delivery_id },
              }"
            >
              <i class="fa-regular fa-pen-to-square"></i>
            </router-link>
            <button
              class="btn btn-primary-soft btn-sm btn-round"
              @click="exportDelivery(item, item.delivery_type)"
            >
              <i class="fa-thin fa-cloud-arrow-down"></i>
            </button>
          </td>
          <!-- <td>
                    <button class="btn btn-primary-soft btn-sm btn-round" @click="exportDelivery(item)">
                        <i class="fa-thin fa-cloud-arrow-down"></i>
                    </button>
                </td> -->
        </tr>
      </tbody>
    </table>

    <div class="row">
      <div class="col-12">
        <pagination
          v-bind:pagination="pagination"
          v-on:click.native="getDelivery(pagination.current_page)"
          :offset="4"
        ></pagination>
      </div>
    </div>
  </div>
</template>

<script>
import modalDelivery from "@/extends/ModalAddDelivery.vue";
import axios from "axios";
import pagination from "../../extends/Paginate";
export default {
  components: {
    modalDelivery,
    pagination,
  },
  data() {
    return {
      deliverys: [],
      company_name: "",
      pagination: {
        total: 0,
        per_page: 2,
        from: 1,
        to: 0,
        current_page: 1,
      },
    };
  },
  created() {
    this.getDelivery(this.pagination.current_page);
  },
  methods: {
    search() {
      this.getDelivery(pagination.current_page);
    },
    show(item) {
      this.$refs.modalDelivery.show(item);
    },
    deliveryType(type) {
      switch (type) {
        case 0:
          return "BBBG";
          break;
        case 1:
          return "Thu Hồi";
        case 2:
          return "Thay đổi thông tin";
      }
    },
    async exportDelivery(delivery, type) {
      this.$econtract.showLoading();
      await axios
        .post(
          "exportdelivery",
          {
            id: delivery.delivery_id,
          },
          {
            responseType: "blob",
          }
        )
        .then((response) => {
          let ext = "xlsx";
          if (type > 0) ext = "docx";

          console.log(response.data);

          let fileName = `${delivery.serial_number}.${ext}`;
          let downloadLink = document.createElement("a");
          downloadLink.href = window.URL.createObjectURL(new Blob([response.data]));
          downloadLink.setAttribute("download", fileName);
          document.body.appendChild(downloadLink);
          downloadLink.click();
          this.$econtract.hideLoading();

          // let fileName = `${detail.partner.partner_name}-${this.types[type].name}`;
          //           let downloadLink = document.createElement("a");
          //           downloadLink.href = window.URL.createObjectURL(new Blob([response.data]));
          //           downloadLink.setAttribute("download", fileName);
          //           document.body.appendChild(downloadLink);
          //           downloadLink.click();
          //           this.$econtract.hideLoading();
        });
    },
    async getDelivery(page) {
      if (this.company_name != "") page = 1;
      this.$econtract.showLoading();
      await axios
        .post("deliverys/search", {
          page: page,
          name: this.company_name,
        })
        .then((response) => {
          this.deliverys = response.data.data;
          this.pagination = response.data;
          this.$econtract.hideLoading();
        });
    },
  },
};
</script>

<style></style>
